    content: "";
    display: inline-block;
    height: 0.5px;
    position: relative;
    vertical-align: middle;
    width: 36%;
  }
  
  .h1:before {
    right: 2.3em;
    margin-left: -52%;  }
  
  .h1:after {
    left: 1.8em;
    margin-right: -50%;

  }

